html, body, #root {
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}

#root {
  display: flex;
  flex-direction: column;
}
.content {
  flex: 1 0 auto;
  position: relative;
}
.footer {
  flex-shrink: 0;
}
.header {
  display: grid;
  grid-template-areas:
  "logo empty cart-menu"
  "menu menu menu";
  position: -webkit-sticky;
  position: sticky;
  width: 100vw;
  background-color: white;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
}

.logo-wrapper {
  width: 100px;
  grid-area: logo;
}

.logo {
  width: 100%;
}

.cart-and-menu {
  grid-area: cart-menu;
  justify-self: right;
  align-self: center;
  display: flex;
  flex-direction: row;
}

.cart {
  position: relative;
  top: 50%;
  align-self: center;
  -webkit-transform: scale(125%);
          transform: scale(125%);
  padding-right: 15px;
}

.cart-quantity {
  position: absolute;
  font-size: 65%;
  background-color: rgba(255,0,0,80%);
  color: white;
  padding: 1px 3px;
  border-radius: 50%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  top: 4px;
  right: 10px;
}

.menu {
  text-align: center;
}

.menu-button {
  all: unset;
  border: none;
  background-color: white;
  margin-right: 15px;
}

.menu-button > svg {
  min-width: 24px;
}

.menu-icon {
  border: 1px solid red;
  justify-self: end;
}

.website-links {
  grid-area: menu;
  max-height: 150px;
  transition: max-height 0.45s ease-out;
  overflow: hidden;
}

.closed {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.45s ease-out;
}

.website-links .link {
  height: 35%;
  padding: 10px;
  font-size: 150%;
}

.website-links .link a {
  transition: color 300ms;
}

.website-links .link a:hover {
  color: rgb(90,90,90);
}

.website-links:not(.closed) {
  border-top: 1px solid rgb(86, 86, 86);
  border-bottom: 1px solid rgb(86, 86, 86);
}

.link:nth-child(odd) {
  border-top: 1px solid rgb(181, 181, 181);
  border-bottom: 1px solid rgb(181, 181, 181);
}

.ext-link-icon {
  padding-left: 5px;
  color:rgb(88, 88, 88);
}

@media (min-width: 1024px) {
  .header > * {
    grid-area: unset;
  }

  .menu-button {
    display: none;
  }

  .header {
    display: flex;
  }

  .website-links {
    max-height: none;
    max-height: initial;
    overflow: visible;
    overflow: initial;
    border: none;
  }

  .website-links > ul {
    display: flex;
    flex-direction: row;
    height: 100%;
    align-items: center;
  }

  .website-links .link {
    border: medium none currentColor;
    border: initial;
    height: auto;
    height: initial;
    padding: 0;
    margin: 0 15px;
  }

  .cart-and-menu {
    position: absolute;
    right: 5%;
  }
}
