  z-index: 1;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 99%;
  -webkit-filter: blur(1px) sepia(0.3);
          filter: blur(1px) sepia(0.3);
}

.our-mission .text {
  position: absolute;
  z-index: 3;
  top: 0;
  padding: 15px;
  width: 80vw;
  max-width: 800px;
  left: 50%;
  right: 50%;
  -webkit-transform: translate(-50%, 15%);
          transform: translate(-50%, 15%);
}

.mission-header, .mission-text {
  color: white;
}

.mission-header {
  text-align: center;
  font-size: 300%;
  -webkit-font-feature-settings: "pcap", "c2pc";
