@media (min-width: 700px) {
  .product-details {
    display: grid;
    grid-template-areas: 
    "title title"
    "carousel desc"
    "carousel desc";
    grid-template-columns: 3fr 2fr;
    max-width: 900px;
    margin: 0 auto;
  }

  .product-title {
    grid-area: title;
  }

  .picture-carousel {
    width: 100%;
    grid-area: carousel;
  }

  .product-price-desc {
    grid-area: desc;
  }

}

.error-message {
  border: 1px solid maroon;
  border-radius: 3px;
  padding: 2px;
  color: maroon;
  -webkit-animation: appear-from-top 0.6s;
          animation: appear-from-top 0.6s;
}


@-webkit-keyframes appear-from-top {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  };
}


@keyframes appear-from-top {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  };
}

.tremble {
  -webkit-animation: tremble 0.6s;
          animation: tremble 0.6s;
}

@-webkit-keyframes tremble {
  0% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  25% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  75% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes tremble {
  0% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  25% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  75% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

.successful-message {
  opacity: 0;
}

.successful-flash {
  color: lime;
  -webkit-animation: flash-temporarily 3s;
          animation: flash-temporarily 3s;
  position: relative;
  left: 5px;
}

@-webkit-keyframes flash-temporarily {
  0% {
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  25% {
    color: rgb(73, 255, 73);
  }
  35% {
    color: lime;
  }
  45% {
    color: rgb(73, 255, 73);
  }
  55% {
    color: lime;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }

}

@keyframes flash-temporarily {
  0% {
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  25% {
    color: rgb(73, 255, 73);
  }
  35% {
    color: lime;
  }
  45% {
    color: rgb(73, 255, 73);
  }
  55% {
    color: lime;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }

}
